.total {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100%;
}

.container {
  /* position: relative; */
  width: 50rem;
  height: 100%;
  background: #fff;
  display: flex;
}

.container .leftside {
  position: relative;
  flex: 30%;
  background: #2163f1;
  color: #fff;
  padding: 10px;
}

.container .rightside {
  position: relative;
  flex: 70%;
  background: #e6e6fa;
  padding-top: 16px;
  padding: 10px;
}
.container .leftside .proimage {
  text-align: center;
}

.container .leftside .proimage img {
  height: 12rem;
  margin-top: 0.8rem;
  border-radius: 50%;
  border: solid rgb(188, 236, 243) 5px;
}
.container .leftside .contact {
  text-align: center;
  font-size: 1rem;
}
.container .leftside .contact .personal {
  font-size: 1.5rem;
  border-bottom: solid white 2px;
}
.container .leftside .contact .tit {
  text-align: left;
  font-weight: bold;
  color: #0afbff;
}
.container .leftside .contact .tites {
  text-align: left;
  font-size: 1rem;
  line-height: 1.2;
  margin-left: 6px;
}

.rigthtit {
  margin-left: 0.5rem;
  font-weight: bold;
  color: rgb(43, 118, 247);
}

.rigthtites {
  text-indent: 25px;
  margin-left: 0.8rem;
  color: rgb(3, 75, 157);
  line-height: 1.6;
  font-size: 1.1rem;
}
.rigthtitess,
.rigthtitesas {
  margin-right: 0.5rem;
  font-size: 0.5rem;
  margin-left: 0.5rem;
  color: rgb(3, 75, 157);
  font-size: 1.1rem;
  line-height: 1.6;
}

table,
th,
td {
  border: 0.5px solid rgb(109, 164, 252);
  font-size: 1rem;
  color: rgb(3, 75, 157);
  text-align: center;
  font-size: 1rem;
}

.Sch {
  font-size: 1.1rem;
  padding: 5px;
  text-align: justify;
}
.yea {
  font-size: 1rem;
  text-align: center;
  line-height: 1.5;
  padding: 5px;
}
.Qua,
.sno {
  font-size: 1rem;
  padding: 5px;
  text-align: center;
}

.acd {
  margin-left: 0.5rem;
  margin-right: 0.5rem;
}
.sci {
  font-size: 0.8rem;
  text-align: center;
}
.cer {
  padding-left: 1rem;
  padding-right: 1rem;
  font-size: 1rem;
}
.ins {
  padding-left: 1rem;
}

@media screen and (max-width: 420px) {
  .total {
    width: 100%;
  }

  .container {
    width: 22rem;
    margin-top: -0.5rem;
    margin-left: -0.7rem;
    margin-right: -0.5rem;
  }

  .container .leftside .proimage img {
    height: 5rem;
    border: solid rgb(188, 236, 243) 2px;
  }
  .container .leftside {
    padding: 8px;
  }
  .container .rightside {
    padding: 0;
  }

  .container .leftside .contact .personal {
    font-size: 0.8rem;
    border-bottom: solid white 2px;
  }
  .container .leftside .contact .tit {
    font-size: 8px;
    margin-top: 20px;
    margin-bottom: 5px;
    padding-left: 5px;
  }
  .container .leftside .contact .tites {
    font-size: 6px;
    margin-bottom: 10px;
    margin-left: 8px;
    line-height: 1;
  }
  #email {
    font-size: 5px;
  }

  .rigthtit {
    font-size: 9px;
    font-weight: bold;
    color: rgb(43, 118, 247);
    margin-bottom: 5px;
    margin-top: 9px;
  }

  .rigthtites,
  .rigthtitesas {
    text-indent: 5px;
    font-size: 8px;
    margin-left: 10px;
    margin-right: 10px;
    text-align: justify;
    margin-bottom: 0;
    line-height: 1.8;
  }
  .rigthtitesas {
    margin-bottom: 1rem;
  }

  .rigthtitess {
    font-size: 8px;
    margin-bottom: 1px;
    line-height: 1.6;
  }
  table {
    margin-bottom: 5px;
    margin-top: 10px;
  }

  table,
  th,
  td {
    border: 0.2px solid rgb(109, 164, 252);
    font-size: 8px;
  }

  .Sch,
  .Qua,
  .sno,
  .yea,
  .sci {
    font-size: 8px;
  }

  .cer,
  .Qsy {
    font-size: 8px;
    text-align: center;
    padding: 5px;
  }
  .ins {
    padding-left: 1rem;
  }
}
